
import './App.scss';
import {productsdata} from './componets/productdata.js';
import {ProductBook} from './componets/productview.js';
import { useState } from 'react';

/**/
/*npm install node-sass*/

function App() {
  const [product, setProduct] = useState(productsdata);
  const [userdata, setUserdata] = useState({Fname:"", Lname:"",email:"",Phone:"",streetaddress:"",ZIPcode:"",Postaldistrict:""})

  return (
    <div className="App">
      <header className="App-header">
        <div className="App-header-background">
          <h1 data-heading="Slide">Swords</h1>
          </div> 
      </header>

      <div>
      <div className='fire'></div>
      <div className='fire2'></div>
            <div className='minebox'>
            <div className='movingsword'>
            <div className='spinner'>
            <div className='superspinner'>
              <img className='imgsword' src="https://static.wikia.nocookie.net/minecraft_gamepedia/images/6/6a/Diamond_Sword_JE2_BE2.png" alt="react logo" />
            </div>
            </div>
            </div>
            </div>
        
            <div className='minebox2'>
            <div className='movingsword2'>
            <div className='spinner'>  
            <div className='superspinner'>
            <div className='rotate_image'>
              <img className='imgsword'  src="https://static.wikia.nocookie.net/minecraft_gamepedia/images/6/6a/Diamond_Sword_JE2_BE2.png" alt="react logo" />
              </div>
            </div>
            </div>
            </div>
            </div>


            <div className='box'></div>
            <div className='ab'>
              <ProductBook product={product} setProduct={setProduct} userdata={userdata} setUserdata={setUserdata}  />
              </div>
      </div>
    
      
      
      
    </div>
  );
}

export default App;
