import './productdata.js';
import '../App.scss';
import {useState,useEffect} from 'react';
import { products } from './productdata.js';
import { waitFor } from '@testing-library/react';



const ProductOBJ = ({product, addProduct}) => {
    const [showTime, setShowTime] = useState(false);
    const [activeId, setActiveId] = useState();
    const [sword10, setsword10] = useState(false);
    const [sword9, setsword9] = useState(false);
    const [sword8, setsword8] = useState(false);
    const [sword7, setsword7] = useState(false);
    const [sword6, setsword6] = useState(false);

    const timerSword10 = () => {
          const timer = setTimeout(()=>{setsword10(!sword10)}, 2000);
          return () => clearTimeout(timer);
      }
    
    return(
      
        <div onMouseEnter={() => {setActiveId(product.id); console.log(activeId)}} >
           
              {activeId === product.id ? (
                  <div>
                  <img className='chain-right' src='https://minecraft-max.net/upload/resize_cache/iblock/bad/320_320_14a821bd1cef7808de0ee325b13280e30/bad21a23bbdeebf9014e82d11c338350.png'/>
                  <img className='chain-left' src='https://minecraft-max.net/upload/resize_cache/iblock/bad/320_320_14a821bd1cef7808de0ee325b13280e30/bad21a23bbdeebf9014e82d11c338350.png'/>
  
                <div className="phonebox">
   
                <button className='button' onClick={()=>{setShowTime(!showTime); console.log(showTime)}}>  {product.name} </button>
                {showTime &&
                <div>
                <p> {product.description}</p>
                </div>
        }
                <p>price: {product.price} <img className='emerald' src='https://static.wikia.nocookie.net/minecraft_gamepedia/images/2/26/Emerald_JE3_BE3.png'/></p>
                <img className='frame' src='https://lh3.googleusercontent.com/vKJmx2a0pzxHowWFGjT3qbZyjvtfZRPYUKFmYJAbgfwLTxaASdQmCvoMDWwo7-G-D5MWivyk4_lgWeBw34MV=s400'></img>
            
                    <button onClick={()=>{addProduct(product.id);if(product.id == 10){
                        {setsword10(!sword10)}
                        //{timerSword10()}

                    }else if(product.id == 9){
                        {setsword9(!sword9)}
                    }else if(product.id == 8){
                        {setsword8(!sword8)}
                    }else if(product.id == 7){
                        {setsword7(!sword7)}
                    }else if(product.id == 6){
                        {setsword6(!sword6)}
                    }else <></>

                    }}  className='shopbutton'>
                        {sword10 && 
                        <img className="img10" alt="profile picture" src={product.picture}/>
                        } {sword9 && 
                        <img className="img9" alt="profile picture" src={product.picture}/>
                        }
                         {sword8 && 
                        <img className="img8" alt="profile picture" src={product.picture}/>
                        }
                         {sword7 && 
                        <img className="img7" alt="profile picture" src={product.picture}/>
                        }
                         {sword6 && 
                        <img className="img6" alt="profile picture" src={product.picture}/>
                        }

                        <img className="img" alt="profile picture" src={product.picture}/></button>

                </div>
                </div>
        ) : (
                <div>
                <img className='chain-right' src='https://minecraft-max.net/upload/resize_cache/iblock/bad/320_320_14a821bd1cef7808de0ee325b13280e30/bad21a23bbdeebf9014e82d11c338350.png'/>
                <img className='chain-left' src='https://minecraft-max.net/upload/resize_cache/iblock/bad/320_320_14a821bd1cef7808de0ee325b13280e30/bad21a23bbdeebf9014e82d11c338350.png'/>

                <div className="phonebox2">
            <button className='button' onClick={()=>{setShowTime(!showTime); console.log(showTime)}}>  {product.name} </button>
            {showTime &&
            <div>
            <p> {product.description}</p>
            </div>
             }
             
            <p>price: {product.price} <img className='emerald' src='https://static.wikia.nocookie.net/minecraft_gamepedia/images/2/26/Emerald_JE3_BE3.png'/></p>
            <img className='frame' src='https://lh3.googleusercontent.com/vKJmx2a0pzxHowWFGjT3qbZyjvtfZRPYUKFmYJAbgfwLTxaASdQmCvoMDWwo7-G-D5MWivyk4_lgWeBw34MV=s400'></img><button onClick={()=>addProduct(product.id)}  className='shopbutton'><img className="img" alt="profile picture" src={product.picture}/></button>
                
          
             </div>
            </div>
             )}
           </div>
          
       
        
    )
}
