const CartOBJ = ({products, minusprodct}) => {
    
        if(products.amount > 0){
            
          return(
            <figure key={products.id}>
            <div className="cart">
                <div>
                    <p>{products.name} {products.amount}</p>
                        <span className='caption'>
                        <button className='cartbut'><img className="imgin" alt="profile picture" src={products.picture}/></button>
                        <p className='nomargin'>price pcs:{products.price}<img className='emerald' src='https://static.wikia.nocookie.net/minecraft_gamepedia/images/2/26/Emerald_JE3_BE3.png'/></p>
                        <button className='minusbutton' onClick={()=>minusprodct(products.id)}><img className='minusimg' src='https://static.vecteezy.com/system/resources/thumbnails/008/506/370/small_2x/negative-sign-icon-outline-red-minus-3d-icon-illustration-free-png.png'/></button>
                        </span>
               
                </div>
            </div>
            </figure>
          )
        } 
        else{return(<></>)}
    
}
const OrderOBJ = ({products}) => {
        if(products.amount > 0){
          return(
            <div key={products.id}>
                
            <figure>
            <div className="cart2">
                <div>
                    <p>{products.name} {products.amount}</p>
                        <span className='caption'>
                        <button className='cartbut2'><img className="imgin" alt="profile picture" src={products.picture}/></button>
                        <p className='nomargin'>price pcs:{products.price} <img className='emerald' src='https://static.wikia.nocookie.net/minecraft_gamepedia/images/2/26/Emerald_JE3_BE3.png'/></p>

                        </span>
                  
                </div>
            </div>
            </figure>
            </div>
          )
        } 
        else{return(<></>)}
    
}


const ProductBook = (props) => {
    
    const [showprice, setShowprice] = useState(false);
    const [showeverything, setShoweverything] = useState(true);
    const [showorder ,setShoworder] = useState(false);
    const [showend ,setShowend] = useState(false);
    const [showerror ,setShowerror] = useState(false);
    /**/ 
    const addProduct = (id) => {
        const tempProducts = props.product.map(c => {
            if(id == c.id){
                c = {...c, amount: c.amount + 1}
            }
            return c
        })
        props.setProduct(tempProducts)
    }

    const minusprodct = (id) => {
        const tProducts = props.product.map(c => {
            if(id == c.id){
                c = {...c, amount: c.amount - 1}
            }
            return c
        })
    
        props.setProduct(tProducts)
    }

    const totalminusprodct = () => {
        const totProducts = props.product.map(c => {
            c = {...c, amount: c.amount = 0}
            
            return c
        })
    
        props.setProduct(totProducts)
    }

    const totalamount = () => {
        return props.product.map(c => c.amount).reduce((a,b)=>a+b,0)
    }

    const totalprice = () => {
        return props.product.map(c => c.price * c.amount).reduce((a,b) =>a+b,0)
    }
    
    const submithandler = () => {
        if (props.userdata.Fname == ""){
            if(showerror == false){
                {setShowerror(!showerror)}
            }else{}
        }else if(props.userdata.Lname == ""){
            if(showerror == false){
                {setShowerror(!showerror)}
            }else{}
        }else if(props.userdata.email == ""){
            if(showerror == false){
                {setShowerror(!showerror)}
            }else{}
        }else if(props.userdata.Phone == ""){
            if(showerror == false){
                {setShowerror(!showerror)}
            }else{}
        }else if(props.userdata.streetaddress == ""){
            if(showerror == false){
                {setShowerror(!showerror)}
            }else{}
        }else if(props.userdata.ZIPcode == ""){
            if(showerror == false){
                {setShowerror(!showerror)}
            }else{}
        }else if(props.userdata.Postaldistrict == ""){
            if(showerror == false){
                {setShowerror(!showerror)}
            }else{}
        }else{
            if(showerror == true){
                {setShowerror(!showerror)}
            }else{
            {setShowend(!showend);setShoworder(!showorder);}
            }
        }
        
   
    }
    
    const setfield = (field,value) => {
        const temp = {...props.userdata}
        temp[field] = value
        props.setUserdata(temp)
    }

    return(
        
        <div className='every' > 
            {showeverything &&
            <div>
                <div className='phonebook'>
                    {props.product.map(c => <ProductOBJ key={c.id} product={c} addProduct={addProduct} />)}
                </div>
          
            <button className='shopbutton4' onClick={()=>{setShowprice(!showprice)}}>Shoping cart {totalamount()}</button> 
            
                {showprice &&
                        <div className='phonebook'>
                            <img className='wood-right'  src='https://lh3.googleusercontent.com/UnZy2-vjf5UgcBaOIZimN2sBdMMBmZQcSS77ROx3lASEFrMX7PBwbVYTYLok6JKgk4LxFsg9pXbaQvDasVhkz3A8vHFSyQQ75Jg=s400'/>
                            <img className='wood-left'  src='https://lh3.googleusercontent.com/UnZy2-vjf5UgcBaOIZimN2sBdMMBmZQcSS77ROx3lASEFrMX7PBwbVYTYLok6JKgk4LxFsg9pXbaQvDasVhkz3A8vHFSyQQ75Jg=s400'/>
                            <button className='clearbutton2' onClick={()=>totalminusprodct()}>Clear Shoping cart</button>
                            <button className='orderbutton2' onClick={()=>{setShoweverything(!showeverything);setShoworder(!showorder);}}>Order</button>
                            <div className='areacart'> 
                                <div className='total'>total price: {totalprice()} <img className='emerald' src='https://static.wikia.nocookie.net/minecraft_gamepedia/images/2/26/Emerald_JE3_BE3.png'/> </div>
                                    {props.product.map(c => <CartOBJ key={c.id}  products={c} minusprodct={minusprodct}/>)}
                           
                        </div>
                </div>}
            </div>}
            {showorder &&
            <div>
                   <img className='chain-right2' src='https://minecraft-max.net/upload/resize_cache/iblock/bad/320_320_14a821bd1cef7808de0ee325b13280e30/bad21a23bbdeebf9014e82d11c338350.png'/>
                  <img className='chain-left2' src='https://minecraft-max.net/upload/resize_cache/iblock/bad/320_320_14a821bd1cef7808de0ee325b13280e30/bad21a23bbdeebf9014e82d11c338350.png'/>
            <div className='Orderinfo'>
            <form onSubmit={submithandler} id='form1'>
                <h3>Order information</h3>
                 <input onChange={e => setfield("Fname",e.target.value)} type="text" name='Fname' value={props.userdata.Fname} maxLength={20} minLength={1} placeholder='First name' required />
                 <input onChange={e => setfield("Lname",e.target.value)} type="text" name='Lname' value={props.userdata.Lname} maxLength={20} minLength={1} placeholder='Last name' required />
                 <input onChange={e => setfield("email",e.target.value)} type="text" name='email' value={props.userdata.email} maxLength={70} minLength={1} placeholder='email' required/>
                 <input onChange={e => setfield("Phone",e.target.value)} type="text" name='phone' value={props.userdata.Phone} maxLength={70} minLength={1} placeholder='phone' required/>
                 <h4>Address</h4>
                 <input onChange={e => setfield("streetaddress",e.target.value)} type="text" name='street address' value={props.userdata.streetaddress} maxLength={70} minLength={1} placeholder='street address' required/>
                 <input onChange={e => setfield("ZIPcode",e.target.value)} type="text" name='ZIP code' value={props.userdata.ZIPcode} maxLength={70} minLength={1} placeholder='ZIP code' required/>
                 <input onChange={e => setfield("Postaldistrict",e.target.value)} type="text" name='Postal district' value={props.userdata.Postaldistrict} maxLength={70} minLength={1} placeholder='Postal district' required/>
                <br></br>
                <button className='orderbutton' type="submit" form="form1" value="Submit" onClick={()=>{submithandler()}}>Submit</button>
                <div className='left'><button className='clearbutton' onClick={()=>{setShoweverything(!showeverything);setShoworder(!showorder); if(showerror == true){setShowerror(!showerror)}}}>Delete order</button></div>
            </form>
            </div></div>}
            {showerror &&
            <div className='error'>
                <p>Fill all field</p>
            </div>}
            {showend &&
            <div>
                  <img className='chain-right2' src='https://minecraft-max.net/upload/resize_cache/iblock/bad/320_320_14a821bd1cef7808de0ee325b13280e30/bad21a23bbdeebf9014e82d11c338350.png'/>
                  <img className='chain-left2' src='https://minecraft-max.net/upload/resize_cache/iblock/bad/320_320_14a821bd1cef7808de0ee325b13280e30/bad21a23bbdeebf9014e82d11c338350.png'/>
            <div className='Order-raport'>
                <table>
                <tbody className='Order-table'>
                <tr>
                    <td>First name:</td>
                    <td>{props.userdata.Fname}</td>
                </tr>
                <tr>
                    <td>Last name:</td>
                    <td>{props.userdata.Lname}</td>
                </tr>
                <tr>
                    <td>email:</td>
                    <td>{props.userdata.email}</td>
                </tr>
                <tr>
                    <td>phone:</td>
                    <td>{props.userdata.Phone}</td>
                </tr>
                <tr>
                    <td>street address:</td>
                    <td>{props.userdata.streetaddress}</td>
                </tr>
                <tr>
                    <td>ZIP code:</td>
                    <td>{props.userdata.ZIPcode}</td>
                </tr>
                <tr>
                    <td>Postal district:</td>
                    <td>{props.userdata.Postaldistrict}</td>
                </tr>
                </tbody>
                </table>
                {props.product.map(c => <OrderOBJ key={c.id} products={c}/>)}
                <br></br>
                <div className='total'>total price: {totalprice()}  <img className='emerald' src='https://static.wikia.nocookie.net/minecraft_gamepedia/images/2/26/Emerald_JE3_BE3.png'/></div>
                <button className='clearbutton' onClick={()=>{setShowend(!showend);setShoweverything(!showeverything);totalminusprodct();setShowprice(!showprice)}}>back</button>
            </div></div>}
        </div>
        
    )
}





export {ProductOBJ, ProductBook};